import React, { Component } from 'react';
import Notice from './Notice'
import Bedsidecard from './Bedsidecard'
import Scheduletodo from './Scheduletodo'
import Calender from './Calender'
import './Scheduleview.scss'
import * as ApiProvider from '../../API/ApiProvider'
import moment from 'moment';

class Scheduleview extends Component {
    state = {
        patientInfo: [],
        pretransinid: null
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const transinid = caseinfo.TRANSINID
        this.setState({ pretransinid: transinid })
        this.getPatientData(transinid)

    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const transinid = caseinfo.TRANSINID
        const { pretransinid } = this.state
        if (pretransinid !== transinid) {
            this.setState({ pretransinid: transinid })
            this.getPatientData(transinid)
        }
    }
    getPatientData(transinid) {
        console.log(transinid)
        ApiProvider.getOnePatient(transinid)
            .then(data => {
                this.setState({ patientInfo: data.data })
            })
            .catch(err => {
                console.log(err)
            })
    }
    render() {
        function monthformat(month) {
            return month + 1
        }
        function dateformat(time) {
            return new Date(time).getFullYear() + "/" + monthformat(new Date(time).getMonth()) + "/" + new Date(time).getDate()
        }
        function exporttime(time) {
            if (time === null) {
                return "--"
            }
            else {
                return dateformat(time)
            }
        }
        const { caseinfo } = this.props
        const { patientInfo } = this.state
        function formatpatientInfo(patientInfo) {
            if (Array.isArray(patientInfo)) {
                if (patientInfo.length > 0) {
                    return {
                        transintime: moment(patientInfo[0].transintime, "YYYYMMDD").format("YYYY/MM/DD"),
                        transouttime: patientInfo[0].transouttime === 'unset' ? "--" : moment(patientInfo[0].transouttime, "YYYYMMDD").format("YYYY/MM/DD")
                    }
                }
            }
            return {
                transintime: "--",
                transouttime: "--"
            }
        }
        const time = formatpatientInfo(patientInfo)

        return (
            <div>
                <div style={{ marginBottom: "5px" }}>
                    <centerstyle>
                        <div className='scheduletitle'>轉入時間:{time.transintime}/轉出時間:{time.transouttime}</div>
                    </centerstyle>
                    <div className='boxshadow'></div>
                </div>
                <div className='schedulelayout'>
                    <Bedsidecard caseinfo={caseinfo}></Bedsidecard>
                    <Notice caseinfo={caseinfo}></Notice>
                    <Calender caseinfo={caseinfo}></Calender>
                    <Scheduletodo caseinfo={caseinfo}></Scheduletodo>
                </div>
            </div>
        );
    }
}

export default Scheduleview
