import X2JS from "x2js"
import moment from 'moment';


export function nisDataValue(NISRow, filterstring, st, et) {
    if (NISRow === null) {
        return 0
    }
    if (Array.isArray(NISRow[filterstring])) {
        const data = NISRow[filterstring]
        // console.log(data)
        if (data === undefined || data === null || data.length === 0) {
            return 0
        }
        if (filterstring === "abp") {
            const SBP = data[0]
            const DBP = data[1]
            if (SBP.length === 0 || DBP.length === 0) {
                return [0, 0]
            }
            const filterSBPData = SBP.filter(function (value, info, index) {
                return value.Time >= st && value.Time < et
            })
            const filterDBPData = DBP.filter(function (value, info, index) {
                return value.Time >= st && value.Time < et
            })
            if (filterSBPData.length === 0 || filterDBPData.length === 0) {
                return [0, 0]
            }
            return [filterSBPData[0].Data, filterDBPData[0].Data]
        }
        const filterData = data.filter(function (value, info, index) {
            return value.Time >= st && value.Time < et
        })
        if (filterData.length === 0) {
            return 0
        }
        // console.log(filterData, filterstring)
        return filterData[0].Data
    }
    return 0
}

export function formatGridTimer(startTime, checkNum, interval) {
    // console.log(startTime, checkNum, interval)
    if (interval === 0) {
        return {
            startTime: startTime + ((checkNum - 1) * (60 * 60 * 1000)),
            endTime: startTime + ((checkNum) * (60 * 60 * 1000))
        }
    }
    if (interval === 1) {
        return {
            startTime: startTime + ((checkNum - 1) * 4 * 60 * 60 * 1000),
            endTime: startTime + ((checkNum) * 4 * 60 * 60 * 1000)
        }
    }
    if (interval === 2) {
        return {
            startTime: startTime + ((checkNum - 1) * 8 * (60 * 60 * 1000)),
            endTime: startTime + ((checkNum) * 8 * (60 * 60 * 1000))
        }
    }
    return {
        startTime: startTime + ((checkNum - 1) * 24 * (60 * 60 * 1000)),
        endTime: startTime + ((checkNum) * 24 * (60 * 60 * 1000))
    }
}

export function formatTimerForVitalSign(pickerDate, interval) {
    function formatInterval(interval, endTime) {
        if (interval === 0) {
            return {
                endTime: endTime,
                startTime: endTime - (24 * 60 * 60 * 1000)
            }
        }
        if (interval === 1) {
            return {
                endTime: endTime,
                startTime: endTime - (3 * 24 * 60 * 60 * 1000)
            }
        }
        if (interval === 2) {
            return {
                endTime: endTime,
                startTime: endTime - (7 * 24 * 60 * 60 * 1000)
            }
        }
        return {
            endTime: endTime,
            startTime: endTime - (14 * 24 * 60 * 60 * 1000)
        }
    }
    const now = moment()
    const sevenInit = moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf()
    if (now >= sevenInit) {
        const endTime = pickerDate + (31 * 60 * 60 * 1000)
        const Timer = formatInterval(interval, endTime)
        return {
            startTime: Timer.startTime,
            endTime: Timer.endTime
        }
    }
    const endTime = pickerDate + (31 * 60 * 60 * 1000) - (24 * 60 * 60 * 1000)
    const Timer = formatInterval(interval, endTime)
    return {
        startTime: Timer.startTime,
        endTime: Timer.endTime
    }

}
export function statusPersion(persionstatus, QMNC, QTWER, Birthday) {
    function BornStatus(QMNC, persionstatus) {
        // console.log(QMNC)
        const QmncWeeks = Number(QMNC.weeks)
        const QmncDays = Number(QMNC.days)
        const CustomBornWeeks = persionstatus === undefined || Number(persionstatus.week) <= 0 ? null : Number(persionstatus.bweek)
        const CustomBornDays = persionstatus === undefined || Number(persionstatus.week) <= 0 ? null : Number(persionstatus.bday)
        if (CustomBornWeeks > 0) {
            return {
                Weeks: CustomBornWeeks,
                Days: CustomBornDays
            }
        }
        // console.log(QmncWeeks)
        if (QmncWeeks > 0) {
            return {
                Weeks: QmncWeeks,
                Days: QmncDays
            }
        }
        return {
            Weeks: undefined,
            Days: undefined
        }
    }
    function WeightStatus(BornWeight, CustomWeight) {
        if (Number(CustomWeight) > 0) {
            return {
                Weight: CustomWeight
            }
        }
        if (Number(BornWeight) > 0) {
            return {
                Weight: BornWeight
            }
        }
        return {
            Weight: undefined
        }
    }
    function BornWeightStatus(weightlist, birthday) {
        if (weightlist === undefined || weightlist === null) {
            return null
        }
        const BornTimeRange = moment(birthday, "YYYYMMDD").add(2, 'd').valueOf()
        // console.log(moment(BornTimeRange).format("YYYYMMDDHHmmss"))
        // console.log(weightlist)
        if (Array.isArray(weightlist)) {
            const findata = weightlist.filter(function (value, index) {
                return moment(value.rec_dat + value.rec_tim, "YYYYMMDDHHmm").valueOf() <= BornTimeRange && value.type === "TW"
            })
            if (findata.length === 0) {
                return 0
            }
            //最後一筆
            else return parseInt(Number(findata[findata.length - 1].value2) * 1000)
        }
        else {
            return parseInt(Number(weightlist.value2) * 1000)
        }
    }
    // console.log(persionstatus)
    const Born = BornStatus(QMNC, persionstatus[0])
    // console.log(Born)
    const QtwerBornWeight = BornWeightStatus(QTWER, Birthday)
    // console.log(persionstatus)
    const CustomWeight = persionstatus.length === 0 || Number(persionstatus[0].bweight) <= 0 ? null : Number(persionstatus[0].bweight) * 1000
    const BornWeight = WeightStatus(QtwerBornWeight, CustomWeight)
    return {
        BornWeight: BornWeight.Weight,
        BornWeeks: Born.Weeks,
        BornDays: Born.Days
    }
}



export function BornStatusRender(Week, Day) {
    if (Week === null || Week === undefined || Week === 0) {
        return null
    }
    if (Day === null || Day === undefined) {
        return null
    }
    return Week.toString() + "+" + Day.toString() + ">"
}
