import React, { Component } from 'react'
import { Tooltip } from 'antd'
import * as ApiGithub from '../../API/ApiGithub'
import moment from 'moment'
import Chronictablecelltooltip from '../ChronicleView/ChronicTableCellTooltipCell'

export default class InfectionlabtableCell extends Component {
    constructor() {
        super()
        this.state = {
            SummaryParse: [],
            visible: false
        }
        this.mouseOut = this.mouseOut.bind(this)
        this.mouseOver = this.mouseOver.bind(this)
    }

    componentDidMount() {
        ApiGithub.getGitHubJson("Summary.json")
            .then(data => {
                this.setState({
                    SummaryParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    componentDidUpdate() {

    }
    CellText() {
        const { element, caseinfo } = this.props
        const { summaryParse } = this.state
        // const start = moment(element.StartTime).format("YYYY/MM/DD HH:mm:ss")
        // const startTimeStamp = moment(element.StartTime).valueOf()
        // const end = moment(element.EndTime).format("HH:mm:ss")
        const title = ["採檢時間", "檢驗項目", "檢體", "報告"]
        const titleCell = title.map(function (value, index) {
            return (
                <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                    {value}
                </div>
            );
        })
        // const objectElement = element
        console.log(element)
        const objectElementCell = element.map((info, index, arr) => {
            return (

                <div key={index} onMouseEnter={this.mouseOver} onMouseLeave={this.mouseOut} >
                    <Chronictablecelltooltip tooltipelement={info.Data} caseinfo={caseinfo} summaryParse={summaryParse} parentDateCallback={this.callbackDateFunction}></Chronictablecelltooltip>
                </div>
            );
        })
        return (
            <div>
                {/* <div>{start}-{end}</div>
                <div>檢驗檢查項目 : {element.title}類</div> */}
                <div style={{ display: "grid", gridTemplateColumns: "100px 150px 150px 200px" }}>
                    {titleCell}
                </div>
                {objectElementCell}
            </div>
        );
    }
    // callbackDateFunction = (tatus) => {
    //     console.log(tatus)
    //     this.mouseOut
    // }
    // onMouse = (status) => {
    //     if (status !== this.state.visible) {
    //         this.setState({ visible: status })
    //     }
    //     // console.log(status)
    // }
    handleVisibleChange = (visible) => {
        console.log('visible: ', visible);
        if (visible) {
            this.setState({ visible: true })
        }
    }
    callbackDateFunction = () => {
        console.log('mouseOut');
        this.setState({
            visible: false,
        })
    }
    mouseOver() {
        this.setState({
            visible: true,
        })
    }

    mouseOut() {
        this.setState({
            visible: false,
        })
    }
    render() {
        function valueFormat(value) {
            if (value === 0 || value === undefined || value === null || value === "") {
                return ""
            }
            return `${value}`
        }
        const { element, borderleftColor } = this.props
        const TitleRender = this.CellText()
        if (Array.isArray(element)) {
            return (
                <Tooltip title={TitleRender} autoAdjustOverflow={true} overlayStyle={{ maxWidth: "1000px" }} onVisibleChange={this.handleVisibleChange} visible={this.state.visible} >
                    <div style={{ cursor: "pointer", backgroundColor: 'rgba(0,120,230,.1)', height: '40px', display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: borderleftColor }}>
                        <div style={{ fontSize: "16px" }}>{valueFormat(element.length) + "筆"}</div>
                    </div>
                </Tooltip>
            );
        }
        return null
    }
}