import React, { Component } from 'react';
import * as Wardstate from '../../Function/Wardstate'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';
import * as ioDataFormat from '../../Module/ioDataFormat'
import Watervaluechart from './Watervaluechart'

export default class WaterValue extends Component {
    state = {
        preCID: null,
        preTINDT: null,
        QDC: []
    }
    formatTINDT(transindt) {
        console.log(transindt)
        const transdtTimestamp = moment(transindt, "YYYYMMDD").valueOf()
        const nowTimstamp = moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf()
        const next = nowTimstamp + (24 * 60 * 60 * 1000)
        if (transdtTimestamp === nowTimstamp) {
            return {
                start: moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf(),
                end: moment(moment(next).format("YYYYMMDD"), "YYYYMMDD").valueOf()
            }
        }
        return {
            start: moment(transindt, "YYYYMMDD").valueOf(),
            end: moment(moment(next).format("YYYYMMDD"), "YYYYMMDD").valueOf()
        }
    }
    componentDidMount() {
        const { caseinfo } = this.props
        const CID = caseinfo.CASEID
        console.log(caseinfo)
        const TINDT = caseinfo.TRANSINDT
        if (CID !== undefined && CID !== null && TINDT !== undefined && TINDT !== null) {
            this.setState({ preCID: CID, preTINDT: TINDT })
            this.apiGet(CID, this.formatTINDT(TINDT))
        }
    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const { preCID, preTINDT } = this.state
        const CID = caseinfo.CASEID
        const TINDT = caseinfo.TRANSINDT
        if (CID !== undefined && CID !== null && TINDT !== undefined && TINDT !== null) {
            if (preCID !== CID || preTINDT !== TINDT) {
                this.setState({ preCID: CID, preTINDT: TINDT })
                this.apiGet(CID, this.formatTINDT(TINDT))
            }
        }
    }
    apiGetForTime(CID, st, end) {
        console.log(CID, st, end)
        const result = ApiProvider.getQDC(CID, st, end)
            .then(data => {
                const parsedata = Wardstate.XM2JSON(data.data)
                console.log(parsedata)
                return parsedata
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        return result
    }
    apiGet(CID, Time) {
        const EndTimeStamp = moment(Time.end)
        const StartTimeStamp = moment(Time.start)
        const Diff = EndTimeStamp.diff(StartTimeStamp, 'days')
        console.log(Diff)
        if (Diff <= 5) {
            const st = moment(Time.start).format("YYYYMMDDHHmm")
            const end = moment(Time.end).format("YYYYMMDDHHmm")
            ApiProvider.getQDC(CID, st, end)
                .then(data => {
                    const parsedata = Wardstate.XM2JSON(data.data)
                    console.log(parsedata)
                    this.setState({ QDC: parsedata })
                })
                .catch(err => {
                    console.log("Andy test getQDC NG " + JSON.stringify(err));
                });
        }
        else {
            const Diff = EndTimeStamp.diff(StartTimeStamp, 'days') + 1
            var promises = [];
            const StartTP = StartTimeStamp.valueOf()
            for (let index = 0; index < Diff; index++) {
                const st = moment(StartTP + (index * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
                const et = moment(StartTP + ((index + 1) * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
                promises.push(this.apiGetForTime(CID, st, et));
            }
            Promise.all(promises)
                .then(data => {
                    console.log(data)
                    var result = []
                    for (let index = 0; index < data.length; index++) {
                        const element = data[index];
                        result = result.concat(element)
                    }
                    console.log(result)
                    this.setState({ QDC: result })
                })
                .catch((e) => {
                    // handle errors here
                });
        }

    }
    render() {
        const { QDC } = this.state
        const { caseinfo } = this.props
        if (QDC.length === 0) {
            return null
        }
        if (caseinfo.TRANSINDT === undefined || caseinfo.TRANSINDT === null) {
            return null
        }
        const IVDate = ioDataFormat.IVData(QDC) //FlootSheet 第一區和第二區
        console.log(IVDate)
        const IVDataFormat = ioDataFormat.DataFormatToWateValue(IVDate, caseinfo.TRANSINDT, caseinfo.WEIGHT, "IV")
        const FeedData = ioDataFormat.FeedData(QDC) //FlootSheet 第三區
        const FeedDataFormat = ioDataFormat.DataFormatToWateValue(FeedData, caseinfo.TRANSINDT, caseinfo.WEIGHT, "Feed")
        const OtherData = ioDataFormat.OtherDataForWater(QDC) //FlootSheet 第四區
        const Other = ioDataFormat.DataFormatToWateValue(OtherData, caseinfo.TRANSINDT, caseinfo.WEIGHT, "Other")
        const WaterValueData = {
            IV: IVDataFormat,
            Feed: FeedDataFormat,
            Other: Other
        }
        console.log(WaterValueData)
        // console.log(IVDataFormat)
        // console.log(FeedDataFormat)
        // console.log(Other)

        return (
            <Watervaluechart WaterValueData={WaterValueData} caseinfo={caseinfo} id_key={"water_id"} svg_key={"water_svg"}></Watervaluechart>
        )
    }
}