export function formatTimerData(dataArray, pickerdate, interval, source) {
    function formatStartTime(source, interval, endTime) {
        if (source === 'Crossection') {
            return endTime - (24 * 60 * 60 * 1000)
        }
        if (interval === 0) {
            return endTime - (32 * 60 * 60 * 1000)
        }
        if (interval === 1) {
            return endTime - (3 * 24 * 60 * 60 * 1000)
        }
        if (interval === 2) {
            return endTime - (7 * 24 * 60 * 60 * 1000)
        }
        return endTime - (14 * 24 * 60 * 60 * 1000)
    }
    function formatInterval(source, interval) {
        if (source === 'Crossection') { return 4 }
        if (interval === 0) { return 32 }
        if (interval === 1) { return 18 }
        if (interval === 2) { return 21 }
        return 14
    }
    function formatStartAndEnd(source, interval, index, StartTime) {
        if (source === 'Crossection') {
            return {
                start: StartTime + (index * 8 * 60 * 60 * 1000),
                end: StartTime + ((index + 1) * 8 * 60 * 60 * 1000)
            }
        }
        if (interval === 0) {
            return {
                start: StartTime + ((index) * 60 * 60 * 1000),
                end: StartTime + ((index + 1) * 60 * 60 * 1000)
            }
        }
        if (interval === 1) {
            return {
                start: StartTime + (index * 4 * 60 * 60 * 1000),
                end: StartTime + ((index + 1) * 4 * 60 * 60 * 1000)
            }
        }
        if (interval === 2) {
            return {
                start: StartTime + (index * 8 * 60 * 60 * 1000),
                end: StartTime + ((index + 1) * 8 * 60 * 60 * 1000)
            }
        }
        return {
            start: StartTime + (index * 24 * 60 * 60 * 1000),
            end: StartTime + ((index + 1) * 24 * 60 * 60 * 1000)
        }
    }
    function formatUrineforTimer(source, interval, urine, sortData, ST, ET) {
        function formatCrossectionUrine(start, end) {
            const nowTime = moment().valueOf()
            console.log(start, end, nowTime)
            if (start <= nowTime && nowTime <= end) {
                return parseInt((nowTime - start) / (60 * 60 * 1000))
            }
            return 8
        }
        if (source === 'Crossection') {
            return {
                start: ST,
                end: ET,
                element: sortData,
                urine: formatCrossectionUrine(ST, ET)
            }
        }
        if (interval === 0) {
            return {
                start: ST,
                end: ET,
                element: sortData,
                urine: urine
            }
        }
        if (interval === 1) {
            return {
                start: ST,
                end: ET,
                element: sortData,
                urine: urine * 4
            }

        }
        if (interval === 2) {
            return {
                start: ST,
                end: ET,
                element: sortData,
                urine: 8
            }
        }
        return {
            start: ST,
            end: ET,
            element: sortData,
            urine: 24
        }
    }
    const EndTime = pickerdate + (31 * 60 * 60 * 1000)
    const StartTime = formatStartTime(source, interval, EndTime)
    var urine = 0
    var filterData = []
    if (Array.isArray(dataArray)) {
        // console.log(dataArray)
        // console.log(formatInterval(source, interval))
        for (let index = 0; index < formatInterval(source, interval); index++) {
            const Timer = formatStartAndEnd(source, interval, index, StartTime)
            const ST = Timer.start
            const ET = Timer.end
            const SortDataArray = dataArray.filter(function (info, i, arr) {
                const time = moment(info.rec_dat.toString().trim() + info.rec_tim.toString().trim(), "YYYYMMDDHHmm").valueOf()
                if (source === 'Crossection') {
                    if (index === 3) {
                        return StartTime <= time && time < EndTime
                    }
                }
                return ST <= time && time < ET
            })
            if (source === 'Crossection') {
                if (index === 3) {
                    filterData.push(formatUrineforTimer(source, interval, 8, SortDataArray, StartTime, EndTime))

                }
                else {
                    filterData.push(formatUrineforTimer(source, interval, 8, SortDataArray, ST, ET))
                }
            }
            else {
                if (SortDataArray.length === 0) {
                    urine = urine + 1
                    filterData.push(formatUrineforTimer(source, interval, 0, SortDataArray, ST, ET))
                }
                else {
                    urine = urine + 1
                    filterData.push(formatUrineforTimer(source, interval, urine, SortDataArray, ST, ET))
                    urine = 0
                }
            }
        }
    }
    return filterData
}

