export function DataFormatToWateValue(Data, transInDT, WightList, filterString) {
    function valueFormat(filterString, element) {
        if (filterString === "IV") {
            return Number(element.inp_val1)
        }
        if (filterString === "Feed") {
            return Number(element.inp_val2)
        }
        if (filterString === "Other") {
            return Number(element.inp_val1)

        }
        return 0
    }
    function formatWaterWeightValue(WaterValue, weightvalue) {
        const Weight = Number(weightvalue)
        if (Weight > 0) {
            return (WaterValue / Weight).toFixed(0)
        }
        return 0
    }
    function formatSumWaterValue(datafilter, filterString) {
        var sumWaterValue = 0
        for (let index = 0; index < datafilter.length; index++) {
            const element = datafilter[index];
            const elementValue = valueFormat(filterString, element)
            if (elementValue > 0) {
                sumWaterValue = sumWaterValue + elementValue
            }
        }
        return sumWaterValue
    }

    function sumCalories(datafilter, weight) {
        var Calories = 0
        for (let index = 0; index < datafilter.length; index++) {
            const element = datafilter[index];
            if (element.item === "進食") {
                if (element.lost !== undefined) {
                    if (element.lost.toString().match("配方奶") !== null) {
                        Calories = Calories + Number(element.inp_val2) * 0.8
                    }
                    if (element.lost.toString().match("母奶") !== null) {
                        Calories = Calories + Number(element.inp_val2) * 0.7

                    }
                }
            }
            else {
                if (element.nam !== undefined) {
                    if (element.nam.toString().match("10%TPN") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("15%TPN") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 1.5
                    }
                    if (element.nam.toString().match("SMOFliquid") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("D10") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                    if (element.nam.toString().match("D12") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 1.2
                    }
                    if (element.nam.toString().match("D5W") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34 * 5
                    }
                    if (element.nam.toString().match("taita") !== null) {
                        Calories = Calories + Number(element.inp_val1) * 0.34
                    }
                }
            }
        }
        console.log(parseInt(Calories / weight))
        if (Number(weight) > 0) {
            return parseInt(Calories / weight)
        }
        return 0
    }
    const end = moment()
    const start = moment(transInDT, "YYYYMMDD")
    const days = end.diff(start, 'days');
    console.log(days)
    var returnObj = []
    if (Array.isArray(Data) && Array.isArray(WightList)) {
        for (let index = 0; index <= days; index++) {
            const startTimestamp = start.valueOf() + (index * 24 * 60 * 60 * 1000)
            const endTimestamp = start.valueOf() + ((index + 1) * 24 * 60 * 60 * 1000)
            const datafilter = Data.filter(function (info, index, arr) {
                const time = moment(info.rec_dat + info.rec_tim, "YYYYMMDDHHmm").valueOf()
                return startTimestamp <= time && time < endTimestamp
            })
            var weight = 0
            for (let index = 0; index < WightList.length; index++) {
                const element = WightList[index];
                const elementTimestamp = moment(element.rec_dat + element.rec_tim, "YYYYMMDDHHmm").valueOf()
                if (elementTimestamp < endTimestamp) {
                    weight = Number(element.value2)
                    break;
                }
            }
            const sumWaterValue = formatSumWaterValue(datafilter, filterString)
            const returnValue = formatWaterWeightValue(sumWaterValue, weight)
            const Calories = sumCalories(datafilter, weight)
            returnObj.push({
                objectList: datafilter,
                weight: weight,
                sumWaterValue: sumWaterValue.toFixed(0),
                WaterWeightValue: returnValue,
                weightList: WightList,
                Calories: Calories,
                startTime:startTimestamp
            })
        }
    }
    return returnObj
}

