import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//Component
import * as Wardstate from '../../Function/Wardstate'
import * as Wardcardvalue from '../WardCardvalue-k'
import { bednumberformat, WardCardgender } from '../../Function/Wardbedfunction'
import './Wardcarddetail.scss'
//image
import editlogo from '../../Image/svg/edit2.svg'
import fileLogo from '../../Image/svg/file1.svg'
import file2Logo from '../../Image/svg/file2.svg'
//antdesign
import { Modal, InputNumber, Button } from 'antd';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment';

var BBornChangeWeek = 0
var BBornChangeDay = 0
var BBornChangeWeight = 0
class Wardcarddetail extends Component {
    state = {
        hoverstate: "none",
        clickstate: false,
        mouse_hover: false,
        edit_hover: false,
        visible: false,
        pbasignfo: null,
        birthdayweight: "N",
        weight: 0,
        bornweek: 0,
        bornday: 0,
        nowweight: 0,
        report: 0,
        nonreport: 0,
        remark: "",
        TRANSINID: "",
        TRANSINDT: "",
        TRANSINTM: "",

        BornChangeWeek: 0,
        BornChangeDay: 0,
        BornChangeWeight: 0,
        modatstatus: true,
        persionstatus: [],

        preHID: null,
        WeightList: []
    };

    componentDidMount() {
        const { data } = this.props
        // console.log(data)
        const HID = data.hisid
        const date = moment(data.birthday, 'YYYYMMDD')
        const TRID = data.transinid
        const CID = data.caseid
        this.setState({ preHID: HID })
        this.API_DATA(HID, date, TRID, CID)
    }

    componentDidUpdate() {
        const { data } = this.props
        const { preHID } = this.state
        const HID = data.hisid
        if (preHID !== HID) {
            const date = moment(data.birthday, 'YYYYMMDD')
            const TRID = data.transinid
            const CID = data.caseid
            this.setState({ preHID: HID })
            this.API_DATA(HID, date, TRID, CID)
        }
    }

    API_DATA(HID, date, TRID, CID) {
        const loginAcc = sessionStorage.getItem('user');
        ApiProvider.getStoredProcedureORDLIST(HID, date.format('YYYY-MM-DD'), "ALL", loginAcc)
            .then(data => {
                const filter = data.data.filter(function (info, index, arr) {
                    const time = moment(info.ORBGNDT.toString().trim() + info.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    return info.ORSTATUS === "31" && time > moment().valueOf() - (24 * 60 * 60 * 1000) && time < moment().valueOf()
                })
                const Reportfilter = data.data.filter(function (info, index, arr) {
                    const time = moment(info.ORBGNDT.toString().trim() + info.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    return info.ORSTATUS === "64" && time > moment().valueOf() - (24 * 60 * 60 * 1000) && time < moment().valueOf()
                })
                this.setState({ nonreport: filter.length, report: Reportfilter.length })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err));
            });
        ApiProvider.getOnePatientRemark(TRID)
            .then(data => {
                this.setState({
                    remark: data.data[0].context
                })
            })
            .catch(err => {
                console.log("Andy test getOnePatientRemark NG " + JSON.stringify(err));
            })
        ApiProvider.getPersionalStatus(HID)
            .then(data => {
                // const Birthday = source.PBIRHDT
                // const QTWER = Wardstate.XM2JSON(source.QTWPER)
                // const QMNC = Wardstate.QMNCXM2JSON(source.QMNC)
                const persionstatus = data.data
                // const BornWeight = Wardstate.statusPersion(persionstatus, QMNC, QTWER, Birthday).BornWeight
                // const BornWeeks = Wardstate.statusPersion(persionstatus, QMNC, QTWER, Birthday).BornWeeks
                // const BornDays = Wardstate.statusPersion(persionstatus, QMNC, QTWER, Birthday).BornDays
                this.setState({ persionstatus: persionstatus })
            })
            .catch(err => { console.log(err) })
        const st = date.format("YYYYMMDDHHmm")
        const et = moment().format("YYYYMMDDHHmm")
        ApiProvider.getQTWPER(CID, st, et)
            .then(data => {
                const QtwerList = Wardstate.XM2JSON(data.data)
                const WeightList = QtwerList.filter(function (info, index, arr) {
                    return info.type === "TW"
                })
                console.log(data.data)
                this.setState({ WeightList: WeightList })
            })
            .catch(err => {
                console.log(err)
            })
    }

    //Module show
    handleOk(HID, name, sex) {
        function formatdata(customdata, rowdata) {
            if (Number(customdata) > 0) {
                //NO Change
                return customdata
            }
            if (Number(rowdata) > 0) {
                return rowdata
            }
            return 0
        }
        console.log(BBornChangeDay, BBornChangeWeek, BBornChangeWeight)
        var week = formatdata(BBornChangeWeek, this.state.BornChangeWeek)
        var day = formatdata(BBornChangeDay, this.state.BornChangeDay)
        var weight = parseFloat((formatdata(BBornChangeWeight, this.state.BornChangeWeight)) / 1000).toFixed(3)
        // console.log(Number(this.state.BornChangeWeek), Number(this.state.BornChangeDay))
        // console.log(parseFloat(Number(this.state.BornChangeWeight) / 1000).toFixed(3))
        ApiProvider.createPatientInfo(HID, name, sex, weight, week, day)
            .then(data => {
                ApiProvider.getPersionalStatus(HID)
                    .then(data => { console.log(data.data); this.setState({ persionstatus: data.data, visible: false, modatstatus: true, BornChangeWeek: week, BornChangeDay: day, BornChangeWeight: Number(weight) * 1000 }) })
                    .catch(err => { console.log(err) })
            })
            .catch(err => { console.log(err) })
    };

    handleCancel = e => {
        this.setState({
            visible: false,
            modatstatus: true
        });
    };
    showModal = () => { this.setState({ visible: true }); };

    Modelrender() {
        function typefrom(from) {
            if (from === 'Custom') {
                return true
            }
            return false
        }
        function BornWeekAndDay(BornWeeks, BornDays) {
            if (BornWeeks === null || BornWeeks === undefined || BornWeeks === "") {
                return []
            }
            return [BornWeeks, BornDays]
        }
        const { data } = this.props
        const { persionstatus } = this.state
        const source = data
        const HID = source.hisid
        const name = source.pnamec
        const sex = source.psex
        const Birthday = source.birthday
        const BornWeight = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bweight) * 1000
        const BornWeeks = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bweek)
        const BornWdays = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bday)
        const BornStatus = BornWeekAndDay(BornWeeks, BornWdays)
        const viewstatus = this.state.modatstatus
        const dataArray = [
            { item: "病床號碼:", value: bednumberformat(parseInt(source.hbednum)) },
            { item: "病歷號碼:", value: source.hisid.toString().trim() },
            { item: "出生週數:", value: BornStatus },
            { item: "生日:", value: moment(Birthday, 'YYYYMMDD').format('YYYY/MM/DD') },
            { item: "出生體重:", value: BornWeight }
        ]
        const dataArrayRender = dataArray.map(function (info, index, arr) {
            if (info.item === "出生體重:") {
                return (
                    <div key={index} >
                        <div key={index} style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                {info.item}
                            </div>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                <InputNumber defaultValue={info.value} onChange={(value) => { BBornChangeWeight = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>g</div>
                            </div>
                        </div>
                        <div style={{ display: viewstatus ? "none" : "block", color: "red" }}>輸入錯誤</div>
                    </div>
                );
            }
            if (info.item === "出生週數:") {
                // console.log(info)
                return (
                    <div key={index}>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                {info.item}
                            </div>
                            <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                                <InputNumber defaultValue={info.value[0]} onChange={(value) => { BBornChangeWeek = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>週</div>
                                <InputNumber defaultValue={info.value[1]} onChange={(value) => { BBornChangeDay = value }}></InputNumber>
                                <div style={{ margin: "0px 5px" }}>日</div>
                            </div>
                        </div>
                        <div style={{ display: viewstatus ? "none" : "block", color: "red" }}>輸入錯誤</div>
                    </div>
                );
            }
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "1fr 2fr", height: "50px" }}>
                    <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                        {info.item}
                    </div>
                    <div style={{ display: "flex", alignItems: "center", fontSize: "16px" }}>
                        {info.value}
                    </div>
                </div>
            );
        })
        return (
            <Modal
                width={400}
                title="填寫病床資訊"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={
                    <div style={{ display: "flex", justifyContent: typefrom(source.type) ? 'space-between' : 'flex-end', alignItems: "center" }}>
                        {/* <Button key="delete" style={typefrom(source.type) ? { border: "1px solid rgba(208,2,27,1)", color: "rgba(208,2,27,1)" } : { display: "none" }} onClick={this.handleCancel}>清床</Button> */}
                        <div>
                            <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                            <Button key="ok" type="primary" onClick={() => { this.handleOk(HID, name, sex) }}>儲存</Button>
                        </div>
                    </div>}>
                {dataArrayRender}
            </Modal>
        );
    }
    BornStatusRender(Week, Day) {
        console.log(Week, Day)
        if (Week === null || Week === undefined) {
            return null
        }
        if (Day === null || Day === undefined) {
            return null
        }
        return Week.toString() + "+" + Day.toString() + ">"
    }
    render() {
        function valueFormat(value) {
            if (value === "" || value === null || value === undefined) {
                return "--"
            }
            return parseInt(value)
        }
        function valueBornWeight(value) {
            if (value === "" || value === null || value === undefined || value === 0) {
                return ""
            }
            return value.toString() + "g > "
        }

        const { data, previewmode, hbedlist } = this.props
        const { persionstatus, WeightList } = this.state
        const source = data
        const Birthday = source.birthday



        // const QTWER = Wardstate.XM2JSON(source.QTWPER)
        // const QMNC = Wardstate.QMNCXM2JSON(source.QMNC)
        // const WList = this.state.WeightList
        const Weight = WeightList.length === 0 ? null : Number(WeightList[0].value2) * 1000
        const BornWeight = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bweight) * 1000
        const BornWeeks = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bweek)
        const BornDays = persionstatus.length === 0 ? undefined : Number(persionstatus[0].bday)

        const remark = this.state.remark
        if (source === undefined) { return null }
        const status = {
            date: moment(Birthday, "YYYYMMDD").valueOf(),
            week: BornWeeks,
            day: BornDays
        }
        const AgeStatus = Wardstate.CalculateAge(status)
        const role = Number(sessionStorage.getItem('role'))

        return (
            <div>
                <div className='wardcarddetailtitle'>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>NI-{bednumberformat(Number(source.hbednum))}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "16px", fontWeight: "600" }}>{source.pnamec.toString().trim()}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109)", fontSize: "14px", fontWeight: "600" }}>{source.hisid}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{WardCardgender(source.psex)}</t3>
                    <t3 style={{ color: "rgba(0, 26, 109, .7)", fontSize: "12px" }}>{moment(Birthday, 'YYYYMMDD').format('MM/DD')}生</t3>
                    {role == 1 && <div onClick={this.showModal} className='wardcarddetileditbutton' style={{ cursor: "pointer" }}>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>}
                </div>
                <div>
                    <div className='wardinfoweeks'>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {Wardstate.BornStatusRender(BornWeeks, BornDays)}<span style={{ fontWeight: "900" }}>{AgeStatus}</span>
                        </t3>
                        <t3 style={{ color: "rgba(0, 26, 109)" }}>
                            {valueBornWeight(BornWeight)}<span style={{ fontWeight: "900" }}>{valueFormat(Weight)}g</span>
                        </t3>
                        <div className='wardinforeportgroup'>
                            <img src={fileLogo} alt='fileLogo'></img>
                            <div style={{ fontSize: "12px" }}>{this.state.report}</div>
                            <img src={file2Logo} alt='file2Logo'></img>
                            <div style={{ fontSize: "12px" }}>{this.state.nonreport}</div>
                        </div>
                    </div>
                    <div style={{ display: previewmode ? "block" : "none", borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <Wardcardvalue.WardCard centerdata={source.centerMonitorData} previewmode={previewmode} PHISTNUM={source.hisid} PCASENO={source.caseid}></Wardcardvalue.WardCard>
                    </div>
                    <div style={{ height: '30px', display: 'grid', justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", position: 'relative', gridTemplateColumns: "2fr 1fr" }}>
                        <div style={{ overflow: "hidden", fontSize: "12px", textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{remark}</div>
                        <Link className='patientviewbutton' to={{ pathname: "/nicu/patient/" + source.hisid, state: { "source": hbedlist, "HISID": source.hisid, "CASEID": source.caseid, "BEDNUMBER": source.hbednum, "TYPE": true, "TRANSINID": source.transinid } }}>
                            <centerstyle>
                                <div style={{ fontSize: "12px" }}>進入病人資料頁面</div>
                            </centerstyle>
                        </Link>
                    </div>
                </div>
                {this.Modelrender()}
            </div>
        );
    }
}

export default Wardcarddetail;
