import React, { Component } from 'react'
import { Tooltip } from 'antd'
import moment from 'moment'
import Chronictablecelltooltip from './ChronicTableCellTooltipCell'

class Chronictablecell extends Component {
    constructor() {
        super()
        this.state = {
            visible: false
        }
        this.mouseOut = this.mouseOut.bind(this)
        this.mouseOver = this.mouseOver.bind(this)
    }

    CellText() {
        const { element, caseinfo, summaryParse } = this.props
        const start = moment(element.StartTime).format("YYYY/MM/DD HH:mm:ss")
        const startTimeStamp = moment(element.StartTime).valueOf()
        const end = moment(element.EndTime).format("HH:mm:ss")
        const title = ["採檢時間", "檢驗項目", "檢體", "報告"]
        const titleCell = title.map(function (value, index) {
            return (
                <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                    {value}
                </div>
            );
        })
        const objectElement = element.element
        const objectElementCell = objectElement.map((info, index, arr) => {
            return (
                <div  key={index} onMouseEnter={this.mouseOver} onMouseLeave={this.mouseOut} >
                    <Chronictablecelltooltip startTimeStamp={startTimeStamp} tooltipelement={info} caseinfo={caseinfo} summaryParse={summaryParse} parentDateCallback={this.callbackDateFunction}></Chronictablecelltooltip>
                </div>
            );
        })
        return (
            <div>
                {/* <div>{start}-{end}</div>
                <div>檢驗檢查項目 : {element.title}類</div> */}
                <div style={{ display: "grid", gridTemplateColumns: "100px 150px 150px 200px" }}>
                    {titleCell}
                </div>
                {objectElementCell}
            </div>
        );
    }
    handleVisibleChange = (visible) => {
        console.log('visible: ', visible);
        if (visible) {
            this.setState({ visible: true })
        }
    }
    callbackDateFunction = () => {
        console.log('mouseOut');
        this.setState({
            visible: false,
        })
    }
    mouseOver() {
        this.setState({
            visible: true,
        })
    }

    mouseOut() {
        this.setState({
            visible: false,
        })
    }
    render() {
        function checkLineColor(lineColor) {
            if (lineColor === null) {
                return '';
            } else {
                return `${lineColor.width} solid ${lineColor.color}`;
            }
        }
        function valueNull(value) {
            if (value === null || value === undefined || value === 0) {
                return ""
            }
            return `${value}筆`
        }
        const { element } = this.props
        const TitleRender = this.CellText()
        if (element.element.length === 0) {
            return (
                <div style={{ height: '40px', display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: checkLineColor(this.props.lineColor) }}></div>
            );
        }

        return (
            <Tooltip title={TitleRender} autoAdjustOverflow={true} overlayStyle={{ maxWidth: "800px" }} onVisibleChange={this.handleVisibleChange} visible={this.state.visible}>
                <div style={{ backgroundColor: 'rgba(0,120,230,.1)', height: '40px', fontSize: "16px", display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', cursor: "pointer", borderLeft: checkLineColor(this.props.lineColor) }}>
                    {valueNull(element.element.length)}
                </div>
            </Tooltip>
        );
    }
}

export default Chronictablecell