import React, { Component } from 'react'
import './Infectionlabtable.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'
import * as LabDataFormat from '../../Module/LabDatafotmat'
import * as FiliterReg from '../../Function/FiliterReg'
import InfectionlabtableCell from './InfectionlabtableCell'
// import { chronicladdataoneday, chroniclabdatathird } from '../../Data/Wardviewdata'
// import { arraychange } from '../../Function/Wardstate'

class Infectionlabtable extends Component {
    state = {
        preHID: null,
        prePickerDate: null,
        RESLAB02: [],
        RESLAB01: [],
        RESSECT: [],
        parser: [],
        pctList: [],
        LabParse: null
    }
    componentDidMount() {
        const { caseinfo, PickerDate } = this.props
        // const startTime = PickerDate + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
        const HID = caseinfo.HISID
        if (HID !== null || HID !== undefined || PickerDate !== null || PickerDate !== undefined) {
            this.setState({
                preHID: HID,
                prePickerDate: PickerDate
            })
            this.getApi(HID, PickerDate)
        }
    }
    componentDidUpdate() {
        const { caseinfo, PickerDate } = this.props
        const { preHID, prePickerDate } = this.state
        const HID = caseinfo.HISID
        const TRDT = caseinfo.TRANSINDT
        if (preHID !== HID || prePickerDate !== PickerDate) {
            this.setState({
                preHID: HID,
                prePickerDate: PickerDate
            })
            this.getApi(HID, PickerDate, TRDT)
        }
    }
    getApi(HID, PickerDate, TRDT) {
        function selectMonthInterval(Datetime) {
            const timestampInterval = new Date().getTime() - Datetime
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }

        const startTime = PickerDate + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
        // const TransinDate = moment(TRDT, "YYYYMMDD").format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(PickerDate)
        const st = moment(startTime).format("YYYY-MM-DD")
        const loginAcc = sessionStorage.getItem('user');
        ApiProvider.getStoredProcedureRESLAB02(HID, st)
            .then(data => {
                this.setState({ RESLAB02: data.data })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESLAB02 NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB01(HID, st)
            .then(data => {
                this.setState({ RESLAB01: data.data })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        ApiGithub.getGitHubJson("Chronic_Lab.json")
            .then(data => {
                this.setState({
                    LabParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
        ApiGithub.getGitHubJson("Labview.json")
            .then(result => {
                console.log(result.data);
                this.setState({ parser: result.data })
                const parseList = result.data
                ApiProvider.getStoredProcedureRESSECT(HID, MonthInterval, "ALL", loginAcc)
                    .then(data => {
                        const reList = data.data
                        // console.log(reList)
                        this.setState({ RESSECT: reList })
                        const filterTime = this.props.PickerDate + (24 * 60 * 60 * 1000)
                        const pctParserData = parseList.filter(function (info, index, arr) {
                            return info.parser_name === "Labview value Procalcitonin"
                        })
                        if (pctParserData.length > 0) {
                            const pctParse = pctParserData[0]
                            const RESSECT_PCT_LIST = FiliterReg.NewListForTimeAndTarget(reList, filterTime, pctParse.mappings)
                            // console.log(pctParse.mappings.key)
                            const pctList = []
                            for (let index = 0; index < RESSECT_PCT_LIST.length; index++) {
                                console.log(RESSECT_PCT_LIST[index])
                                pctList.push(this.findLastestTargetValue(RESSECT_PCT_LIST[index], "procalcitonin", "pct", parseList))
                            }
                            Promise.all(pctList)
                                .then(data => {
                                    console.log(data)
                                    var result = []
                                    for (let index = 0; index < data.length; index++) {
                                        const element = data[index];
                                        result = result.concat(element)
                                    }
                                    console.log(result)
                                    this.setState({ pctList: result })
                                })
                                .catch((e) => {
                                    // handle errors here
                                });

                        }

                    })
                    .catch(err => { console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err)) });
            })
            .catch(err => {
                console.log("getGitHubJson Labview.json NG " + JSON.stringify(err));
            })
    }
    findLastestTargetValue(item, target, key, parser) {
        //arr === parse ressect
        // const { parser } = this.state
        const selected_parser = parser.find(item => item.apply_condition.name.match(target) !== null);
        console.log(selected_parser)
        console.log(item)
        if (item !== null && item !== undefined) {
            const HID = item.ORHISTNO.toString().trim()
            const CID = item.ORDSEQCN.toString().trim()
            const MM = item.ORMM.toString().trim()
            const SEQ = item.ORDSEQNO.toString().trim()
            // console.log(HID, CID, MM, SEQ, target)
            const parserResult = ApiProvider.getStoredProcedureRESDISP(HID, MM, CID, SEQ)
                .then(data => {
                    const RESDISP = data.data
                    // console.log(RESDISP, target)
                    if (Array.isArray(RESDISP)) {
                        const selected_mapping = selected_parser.mappings.find(mapping => mapping.key === key)
                        const re = new RegExp(selected_mapping.regex_str, selected_mapping.regex_flags)
                        // console.log(re, target)
                        var matchValue = null
                        var currentItemTimestamp = null
                        RESDISP.forEach(element => {
                            const match = element.CONTENT.toString().match(re)
                            // console.log(match, target)
                            if (match !== null) {
                                console.log(match)
                                const currentTimeStr = item.ORRCPDT + "-" + item.ORRCPTM
                                currentItemTimestamp = moment(currentTimeStr.trim(), "YYYY-MM-DD-HH.mm.ss").valueOf()
                                matchValue = match[selected_mapping.position]
                                // console.log(currentTimeStr, currentItemTimestamp, matchValue)
                                // return { value: matchValue, time: currentItemTimestamp }
                            }
                        })
                        return { Data: matchValue, Time: currentItemTimestamp }
                    }
                })
                .catch(err => {
                    console.log(err);
                });
            // console.log(parserResult)
            return parserResult
        }
    }
    formatLabData() {
        const { LabParse, RESSECT } = this.state
        const { PickerDate } = this.props
        const end = PickerDate + 31 * 60 * 60 * 1000
        const start = end - 7 * 24 * 60 * 60 * 1000
        if (LabParse !== null && LabParse !== undefined && RESSECT.length > 0) {
            const parserCultivate = LabParse.Cultivate
            const CultivateList = RESSECT.filter(function (info, index, arr) {
                const parserString = info.ORPROCED
                const time = moment(info["ORBGNDT"].toString().trim() + info["ORBGNTM"].toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                if (Array.isArray(parserCultivate)) {
                    const parserCultivateStatus = parserCultivate.some(function (v, i, a) {
                        const parserRex = new RegExp(v, "ism")
                        return parserString.match(parserRex) !== null
                    })
                    console.log()
                    return parserCultivateStatus && start <= time && time < end
                }
            })
            const mapCultivateList = CultivateList.map(function (info, index, arr) {
                return {
                    Data: info,
                    Time: moment(info["ORBGNDT"].toString().trim() + info["ORBGNTM"].toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                }
            })
            return mapCultivateList
        }
    }
    switch_state() {
        const chronic_state = 2
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 18
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    table_content() {
        function CulFormatList(Datalist, st) {
            var Render = []
            if (Datalist === undefined || Datalist === null) {
                return []
            }
            for (let index = 0; index < 21; index++) {
                const start = st + (index * 8 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 8 * 60 * 60 * 1000)
                const FilterData = Datalist.filter(function (value, index, arr) {
                    return value.Time > start && value.Time < end
                })
                Render.push(FilterData)
            }
            return Render
        }
        function FormatList(Datalist, st) {
            var Render = []
            for (let index = 0; index < 21; index++) {
                const start = st + (index * 8 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 8 * 60 * 60 * 1000)
                const FilterData = Datalist.filter(function (value, index, arr) {
                    return value.Time > start && value.Time < end
                })
                var sum = ""
                for (let index = 0; index < FilterData.length; index++) {
                    const element = FilterData[index];
                    if (index === 0) {
                        sum = element.Data
                    }
                    else {
                        sum = sum + '\n' + element.Data
                    }
                }
                Render.push(sum)
            }
            return Render
        }
        function CellRander(List, type, caseinfo) {
            function checkLineColor(lineColor) {
                if (lineColor === null) {
                    return '';
                } else {
                    return `${lineColor.width} solid ${lineColor.color}`;
                }
            }
            function valueFormat(value) {
                if (value === 0 || value === undefined || value === null || value === "") {
                    return ""
                }
                return `${value}`
            }
            function TimeLine(interval, index) {
                const lineStyle = {
                    width: '',
                    color: '',
                }
                if (interval === 0) {
                    if (index === 0 || (index % 24) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else if (index % 8 === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                    else {
                        lineStyle.width = '1px';
                        lineStyle.color = '#eeeeee';
                        return lineStyle;
                    }
                }
                if (interval === 1) {
                    if (index === 0 || (index % 6) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else if (index % 2 === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                    else {
                        lineStyle.width = '1px';
                        lineStyle.color = '#eeeeee';
                        return lineStyle;
                    }
                }
                if (interval === 2) {
                    if (index === 0 || (index % 3) === 0) {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    } else {
                        lineStyle.width = '2px';
                        lineStyle.color = 'rgba(0,0,0,0.2)';
                        return lineStyle;
                    }
                }
                if (interval === 3) {
                    lineStyle.width = '2px';
                    lineStyle.color = 'rgba(0,0,0,0.2)';
                    return lineStyle;
                }
            }
            if (type === 1) {
                const cellR = List.map(function (info, index) {
                    const lineColoe = TimeLine(3, index)
                    if (Number(info.length) > 0) {
                        return (
                            <InfectionlabtableCell key={index} caseinfo={caseinfo} element={info} borderleftColor={checkLineColor(lineColoe)}></InfectionlabtableCell>
                        );
                    }
                    return (
                        <div style={{ height: '40px', display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: checkLineColor(lineColoe) }}></div>
                    )

                })
                return cellR
            }
            const cellR = List.map(function (info, index) {
                console.log(info)
                const lineColoe = TimeLine(3, index)
                if (Number(info) > 0) {
                    return (
                        <div key={index} style={{ backgroundColor: 'rgba(0,120,230,.1)', height: '40px', fontSize: "16px", display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: checkLineColor(lineColoe) }}>
                            <t2>{valueFormat(info)}</t2>
                        </div>
                    );
                }
                return (
                    <div style={{ height: '40px', display: "flex", justifyContent: "center", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: checkLineColor(lineColoe) }}></div>
                )

            })
            return cellR

        }
        const { PickerDate, title, caseinfo } = this.props
        const { RESLAB02, RESLAB01 } = this.state
        const st = PickerDate + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
        const et = PickerDate + (31 * 60 * 60 * 1000)

        const WBC = LabDataFormat.LabDataFormat(RESLAB02, st, et, "RSV01")
        const Band = LabDataFormat.LabDataFormat(RESLAB02, st, et, "RSV13")
        const Seq = LabDataFormat.LabDataFormat(RESLAB02, st, et, "RSV14")
        const Lym = LabDataFormat.LabDataFormat(RESLAB02, st, et, "RSV15")
        // const PCT = LabDataFormat.LabDataFormat(RESLAB02, st, et, "RSV01")
        const CRP = LabDataFormat.LabDataFormat(RESLAB01, st, et, "RSV30")

        const CultivateList = this.formatLabData()
        console.log(CultivateList)
        const CulFormatListRender = CulFormatList(CultivateList, st)
        console.log(CulFormatListRender)
        const Culcell = CellRander(CulFormatListRender, 1, caseinfo)

        var WBCRender = FormatList(WBC, st)
        var BandRender = FormatList(Band, st)
        var SegRender = FormatList(Seq, st)
        var LymRender = FormatList(Lym, st)
        var PCTRender = FormatList(this.state.pctList, st)
        var CRPRender = FormatList(CRP, st)
        const WBCcell = CellRander(WBCRender, 0, caseinfo)
        const Bandcell = CellRander(BandRender, 0, caseinfo)
        const Segcell = CellRander(SegRender, 0, caseinfo)
        const Lymcell = CellRander(LymRender, 0, caseinfo)
        const PCTcell = CellRander(PCTRender, 0, caseinfo)
        const CRPcell = CellRander(CRPRender, 0, caseinfo)
        return (
            <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{WBCcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Bandcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Segcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Lymcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{PCTcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{CRPcell}</div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>{Culcell}</div>
            </div>
        );
    }
    render() {
        const { userdata, title } = this.props
        const title_list = title.map((info, index) => <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "40px", border: "1px solid rgba(238,238,238,1)", borderCollapse: "collapse", backgroundColor: "#eeeeee" }} key={index}><t2>{info.title}</t2></div>)
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {title_list}
                </div>
                {this.table_content()}
            </div>
        );
    }
}

export default Infectionlabtable
