import React, { useState, useEffect } from "react";
import { statecolor } from '../Function/Wardstate'
import Wardcarstatus from './Wardcardstatus'
import * as Wardstate from "../Function/Wardstate";
import * as ApiProvider from '../API/ApiProvider'
import './Wardcardvalue.scss'
import moment from 'moment';
import { Icon } from 'antd';


export function WardCard(props) {
    const [CenterMonitorValue, SetCenterMonitorValue] = useState(null)
    // const [HID, SetHID] = useState("")
    const [Timer, SetTimer] = useState(0)
    const [abpStateTimer, SetabpStateTimer] = useState(0)
    const [ChangeAN, SetChangeAN] = useState(false)
    const [CenterNisValue, SetCenterNisValue] = useState(null)
    const [AbpCenterMonitorValue, SetAbpCenterMonitorValue] = useState([])
    const [QDVS, SetQDVS] = useState([])
    const [Temperature, SetTemperature] = useState([])

    useEffect(() => {
        const TimerID = setInterval(() => {
            SetTimer(Timer + 1)
        }, 1000 * 5)
        return () => clearInterval(TimerID);
    }, [Timer])

    useEffect(() => {
        if (abpStateTimer <= 60) {
            const abpTimer = setInterval(() => {
                SetTimer(abpStateTimer + 1)
            }, 1000 * 5)
            return () => clearInterval(abpTimer);
        }
    }, [abpStateTimer])

    useEffect(() => {
        const hisid = props.PHISTNUM.toString().trim()
        ApiProvider.getHisidwardvalue(hisid)
            .then(data => {
                const source = data.data
                SetCenterMonitorValue(source[0]);
            })
            .catch(err => {
                console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
            });
    }, [Timer]);

    useEffect(() => {
        const CID = props.PCASENO.toString().trim()
        const end = moment().format("YYYYMMDDHHmm")
        const start = moment().add(-1, 'd').format("YYYYMMDDHHmm")
        // console.log(CID, start, end)
        ApiProvider.getQDVS(CID, start, end)
            .then(data => {
                const parsedata = Wardstate.XM2JSON(data.data)
                const filterparseData = parsedata.filter(function (info, index, arr) {
                    function DBPvalue(info) {
                        if (info.diastolic === null || info.diastolic === undefined || info.diastolic === 0 || info.diastolic.__text !== undefined) {
                            return false
                        }
                        return true
                    }
                    function SBPvalue(info) {
                        if (info.systolic === null || info.systolic === undefined || info.systolic === 0 || info.systolic.__text !== undefined) {
                            return false
                        }
                        return true
                    }
                    return DBPvalue(info) === true && SBPvalue(info) === true
                })
                const filterBTData = parsedata.filter(function (info, index, arr) {
                    function BTvalue(info) {
                        if (info.temperature === null || info.temperature === undefined || info.temperature === 0 || info.temperature.__text !== undefined) {
                            return false
                        }
                        return true
                    }
                    return BTvalue(info) === true
                })
                function formatBTValue(BTarray) {
                    if (Array.isArray(BTarray)) {
                        if (BTarray[BTarray.length - 1].temperature !== undefined) {
                            return BTarray[0].temperature
                        }
                        return null
                    }
                    return null
                }
                SetQDVS(filterparseData)
                SetTemperature(formatBTValue(filterBTData))
            })
            .catch(err => { console.log(err) })
    }, [props.PHISTNUM.toString().trim()])

    // useEffect(() => {
    //     const hisid = props.PHISTNUM.toString().trim()
    //     const endtime = moment().format("YYYYMMDDHHmmss")
    //     const starttime = moment().add(-5, 'm').format("YYYYMMDDHHmmss");
    //     ApiProvider.getAbpCenterMonitorValue(hisid, starttime, endtime)
    //         .then(data => {
    //             console.log(data.data)
    //             SetAbpCenterMonitorValue(data.data)
    //         })
    //         .catch(err => {
    //             console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
    //         });
    // }, [abpStateTimer]);


    useEffect(() => {
        const hisid = props.PHISTNUM.toString().trim()
        const endtime = moment().format("YYYYMMDDHHmmss")
        const starttime = moment().add(-5, 'm').format("YYYYMMDDHHmmss");
        // console.log(hisid, starttime, endtime)
        ApiProvider.getAbpCenterMonitorValue(hisid, starttime, endtime)
            .then(data => {
                // console.log(data.data)
                SetAbpCenterMonitorValue(data.data)
            })
            .catch(err => {
                console.log("Andy test getHisidwardvalue NG " + JSON.stringify(err));
            });
    }, [abpStateTimer]);

    function decideABP() {
        // console.log(CenterNisValue)
        const status = abpStatus(AbpCenterMonitorValue)
        if (status) {
            const abpNewListData = AbpCenterMonitorValue[AbpCenterMonitorValue.length - 1]
            const abps = abpNewListData.abps === undefined || abpNewListData.abps === null || abpNewListData.abps === 0 ? "--" : abpNewListData.abps
            const abpd = abpNewListData.abpd === undefined || abpNewListData.abpd === null || abpNewListData.abpd === 0 ? "--" : abpNewListData.abpd
            const abpm = abpNewListData.abpm === undefined || abpNewListData.abpm === null || abpNewListData.abpm === 0 ? "--" : abpNewListData.abpm
            return InitCenterData(abps) + "/" + InitCenterData(abpd) + "(" + InitCenterData(abpm) + ")"
        } else {
            if (Array.isArray(QDVS)) {
                if (QDVS === null || QDVS === undefined || QDVS.length === 0) {
                    return "--"
                }
                // console.log(QDVS)
                const row = QDVS[0]
                const dBP = row.diastolic
                const sBP = row.systolic
                const mBP = parseInt((Number(dBP) + Number(sBP)) / 2)
                return InitCenterData(sBP) + "/" + InitCenterData(dBP)
            }
            return '--'
        }
    }
    function InitCenterData(value) {
        if (value === undefined || value === null || value === "" || value === 0) {
            return "--"
        }
        else return value
    }
    if (CenterMonitorValue === null || CenterMonitorValue === undefined) {
        return null
    }
    function abpStatus(abpList) {
        if (Array.isArray(abpList)) {
            if (abpList.length === 0) {
                return false
            }
            return true
        }
        return false
    }
    return (
        <div className='Wardcardvaluelayout'>
            <div className='Wardcardvitalvalue'>
                <div className='valueRows' style={{ marginRight: "16px" }}>
                    <div >
                        <t4 style={{ display: "block", color: statecolor('HR'), }}>HR</t4>
                        <t2 className="bothWordValue" style={{ color: statecolor('HR') }}>{InitCenterData(CenterMonitorValue.hr)}</t2>
                    </div>
                    <div >
                        <t4 style={{ display: "block", color: statecolor('SpO2') }}>SpO<sub>2</sub></t4>
                        <t2 className="bothWordValue" style={{ color: statecolor('SpO2') }}>
                            {InitCenterData(CenterMonitorValue.spo2)}
                            <span style={{ fontWeight: "400", fontSize: "12px" }}>%</span>
                        </t2>
                    </div>
                </div>
                <div className='valueRows'>
                    <div >
                        <t4 style={{ display: "block", color: statecolor('BP') }}>{abpStatus(AbpCenterMonitorValue) ? "ABP" : "BP"}</t4>
                        <t2 className="bothWordValue" style={{ color: statecolor('BP') }}>{decideABP()}</t2>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnsGap: "5px" }}>
                        <div >
                            <t4 style={{ display: "block", color: statecolor('RR') }}>RR</t4>
                            <t2 className="bothWordValue" style={{ color: statecolor('RR') }}>{InitCenterData(CenterMonitorValue.rr)}</t2>
                        </div>
                        <div >
                            <t4 style={{ display: "block", color: statecolor('BT') }}>BT</t4>
                            <t2 className="bothWordValue" style={{ color: statecolor('BT') }}>
                                {InitCenterData(Temperature)}
                                <span style={{ fontWeight: "400", fontSize: "12px" }}>&deg;C</span>
                            </t2>
                        </div>
                    </div>
                </div>
            </div>
            <Wardcarstatus source={true} PCASENO={props.PCASENO} PHISTNUM={props.PHISTNUM}></Wardcarstatus>
        </div>
    );
}
