    NISApiGet(PickerDate, PCID, interval) {
        function starTime(interval, endTimer) {
            if (interval === 0) {
                return endTimer - 24 * 60 * 60 * 1000
            }
            if (interval === 1) {
                return endTimer - 3 * 24 * 60 * 60 * 1000
            }
            if (interval === 2) {
                return endTimer - 7 * 24 * 60 * 60 * 1000
            }
            return endTimer - 14 * 24 * 60 * 60 * 1000
        }
        const endTimestamp = PickerDate + 31 * 60 * 60 * 1000
        const startTimestamp = starTime(interval, endTimestamp)
        const et = moment(endTimestamp).format("YYYYMMDDHHmm")
        const st = moment(startTimestamp).format("YYYYMMDDHHmm")
        console.log(PCID, st, et)
        const QDVS = ApiProvider.getQDVS(PCID, st, et)
            .then(data => {
                return Wardstate.XM2JSON(data.data)
            })
            .catch(err => { console.log(err) })

        const QOXY = ApiProvider.getQOXY(PCID, st, et)
            .then(data => {
                return Wardstate.XM2JSON(data.data)

            })
            .catch(err => { console.log(err) })
        Promise.all([QDVS, QOXY]).then(result => {
            function FormatNISData(QDVS, QOXY, type) {
                var HRNISLIST = []
                var RRNISLIST = []
                var SBPNISLIST = []
                var DBPNISLIST = []
                var BTNISLIST = []
                var Spo2NISLIST = []
                if (Array.isArray(QDVS)) {
                    for (let index = 0; index < QDVS.length; index++) {
                        const element = QDVS[index];
                        const hr = element.pluse
                        const rr = element.breath
                        const SBP = element.systolic
                        const DBP = element.diastolic
                        const BT = element.temperature
                        const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                        if (hr !== undefined) {
                            if (Number(hr) !== NaN && hr.__text === undefined) {
                                HRNISLIST.push({
                                    Data: hr,
                                    Time: time
                                })
                            }
                        }
                        if (rr !== undefined) {
                            if (Number(rr) !== NaN && rr.__text === undefined) {
                                RRNISLIST.push({
                                    Data: rr,
                                    Time: time
                                })
                            }
                        }
                        if (SBP !== undefined) {
                            if (Number(SBP) !== NaN && SBP.__text === undefined) {
                                SBPNISLIST.push({
                                    Data: SBP,
                                    Time: time
                                })
                            }
                        }
                        if (DBP !== undefined) {
                            if (Number(DBP) !== NaN && DBP.__text === undefined) {
                                DBPNISLIST.push({
                                    Data: DBP,
                                    Time: time
                                })
                            }
                        }
                        if (BT !== undefined) {
                            // console.log(Number(BT))
                            // console.log(BT.toString().trim())
                            if (Number(BT) !== NaN && BT.__text === undefined) {
                                BTNISLIST.push({
                                    Data: BT,
                                    Time: time
                                })
                            }
                        }
                    }
                }
                if (Array.isArray(QOXY)) {
                    for (let index = 0; index < QOXY.length; index++) {
                        const element = QOXY[index];
                        const spo2 = element.concen
                        if (spo2 !== undefined) {
                            if (Number(spo2) !== NaN && spo2.__text === undefined) {
                                const time = moment(element.rec_dat.toString() + element.rec_tim.toString(), "YYYYMMDDHHmm").valueOf()
                                Spo2NISLIST.push({
                                    Data: spo2,
                                    Time: time

                                })
                            }
                        }
                    }
                }
                if (type === "hr") { return HRNISLIST }
                if (type === "rr") { return RRNISLIST }
                if (type === "spo2") { return Spo2NISLIST }
                if (type === "abp") { return [SBPNISLIST, DBPNISLIST] }
                if (type === "bt") { return BTNISLIST }
                return []
            }
            console.log(result[0], result[1])
            const nisData = {
                hr: FormatNISData(result[0], result[1], "hr"),
                rr: FormatNISData(result[0], result[1], "rr"),
                spo2: FormatNISData(result[0], result[1], "spo2"),
                abp: FormatNISData(result[0], result[1], "abp"),
                bt: FormatNISData(result[0], result[1], "bt")
            }
            console.log(nisData)
            this.setState({ NIS: nisData })
        })
    }
