//累積報告

export function ListForTime(arr, time) {
    var DataList = []
    const pickerTime = time + (24 * 60 * 60 * 1000)
    for (let index = 0; index < arr.length; index++) {
        const element = arr[index];
        // console.log(element)
        const objectTime = moment(element.ORRESDT.toString().trim() + element.ORRESTM.toString().trim(), "YYYY-MM-DDHH.mm.ss").valueOf()
        if (objectTime <= pickerTime) {
            DataList.push(element)
        }
    }
    return DataList
}

export function ListFormat(List) {
    // console.log(List)
    if (Array.isArray(List)) {
        if (List.length === 0) {
            return null
        }
        return List[0]
    }
    return null
}

export function NewListForTimeAndTarget(arr, time, parseMap) {
    if (Array.isArray(arr)&&Array.isArray(parseMap)) {
        const parseMapList = parseMap.map(function(info,index,arr){
            const re = new RegExp(info.key,info.regex_flags)
            return re
        })
        const filterArr = arr.filter(function(info,index,array){
            const mapString = info.ORPROCED.toString().trim()
            const timeElement = moment(info.ORBGNDT.toString().trim()+info.ORBGNTM.toString().trim(),'YYYY-MM-DDHH:mm:ss').valueOf()
            const status = parseMapList.some(function(value,i,a){
                return mapString.match(value)!==null
            })
            return timeElement<=time && status===true
        })
        return filterArr
    }
    return null
}


export function ListForTimeAndTarget(arr, time, target) {
    const targetFormat = target.toLowerCase()
    //arr RESSECT time
    if (Array.isArray(arr)) {
        const filterArr = arr.filter(function(info,index,array){
            const mapString = info.ORPROCED.toString().trim().toLowerCase()
            const timeElement = moment(info.ORBGNDT.toString().trim()+info.ORBGNTM.toString().trim(),'YYYY-MM-DDHH:mm:ss').valueOf()
            return timeElement<=time && mapString.match(targetFormat)!==null
        })
        return filterArr
    }
    return null
}

export function CaRegString(ressectobject) {
    var select = []
    if (Array.isArray(ressectobject)) {
        for (let index = 0; index < ressectobject.length; index++) {
            const element = ressectobject[index];
            const elementstring = element.ORPROCED.toString()
            if (elementstring.match("Ca++, free") !== null) {
                select.push(element)
            }
        }
    }
    if (select.length === 0) {
        return "null"
    }

}

export function QSPERegList(qspe) {
    function SettingValue(mode, value) {
        //mode === title
        console.log(mode, value)
        if (value === undefined || value === null) {
            return ""
        }
        const valueList = value.split("/")
        if (mode === 'nc') {
            return (<div>Flow:{valueList[0]}L/min</div>)
        }
        if (mode === 'hf') {
            return (<div style={{ display: "inline" }}>FiO<sub>2</sub>:{valueList[0]}% ,PEEP:{valueList[1]}cmH<sub>2</sub>O</div>)
        }
        if (mode === 'uw' || mode === 'bcpap' || mode === 'cpap') {
            return (<div style={{ display: "inline" }}>FiO<sub>2</sub>:{valueList[0]}% ,PEEP:{valueList[1]}cmH<sub>2</sub>O</div>)
        }
        if (mode === 'nippv' || mode.match("pc") !== null) {
            // return (<sub>123</sub>)
            return (<div style={{ display: "inline" }}>FiO<sub>2</sub>:{valueList[0]}% ,Rate:{valueList[1]}/min ,PIP:{valueList[2]}mmH<sub>2</sub>O ,PEEP:{valueList[3]}cmH<sub>2</sub>O</div>)
        }
        if (mode.match("hfov") !== null) {
            return (<div style={{ display: "inline" }}>FiO<sub>2</sub>:{valueList[0]}% ,Amp:{valueList[1]}cmH<sub>2</sub>O ,MAP:{valueList[2]}cmH<sub>2</sub>O ,Freq:{valueList[3]}Hz</div>)
        }
        if (mode.match("hfppv") !== null) {
            return (<div style={{ display: "inline" }}>FiO<sub>2</sub>:{valueList[0]}% ,Rate:{valueList[1]}/min ,PIP:{valueList[2]}mmH<sub>2</sub>O ,PEEP:{valueList[3]}cmH<sub>2</sub>O"</div>)
        }
        return value
    }
    function specialValue(DataArray){
        if (Array.isArray(DataArray)) {
            if (DataArray.length===1) {
               return{
                   title:DataArray[0],
                   value:null
               } 
            }
            return{
                title:DataArray[1],
                value:DataArray[2]
            } 
        }
        return{
            title:null,
            value:null
        } 
    }
    var qspearray = []
    if (Array.isArray(qspe)) {
        for (let index = 0; index < qspe.length; index++) {
            const element = qspe[index];
            if (element !== undefined) {
                const SelectElement = element.special.toString()
                if (SelectElement.match("@v") !== null||SelectElement.match("@V") !== null) {
                    const DataArray = SelectElement.split(" ")
                    const dataValue = specialValue(DataArray)
                    const title = dataValue.title
                    const value = dataValue.value
                    const DataJson = {
                        "Mode": title,
                        "Setting": SettingValue(title.toString().toLowerCase(), value),
                        "Date": element.rec_dat.toString().trim(),
                        "Time": element.rec_tim,
                    }
                    qspearray.push(DataJson)
                }
            }
        }
    }
    return qspearray
}

export function QSPESetting(qspesettingarray) {
    const regString = qspesettingarray.Mode
    const regValue = qspesettingarray.Setting.split("/")
    if (regString.match("RA") !== null) {
        return ""
    }
    if (regString.match("NC") !== null) {
        return "Flow:" + regValue[0]
    }
    if (regString.match("UW") !== null) {
        return "FiO2:" + regValue[0] + "/PEEP:" + regValue[1]
    }
    if (regString.match("bcpap") !== null) {
        return "FiO2:" + regValue[0] + "/PEEP:" + regValue[1]
    }
    if (regString.match("CPAP") !== null) {
        return "FiO2:" + regValue[0] + "/PEEP:" + regValue[1]
    }
    if (regString.match("NIPPV") !== null) {
        return "FiO2:" + regValue[0] + "/Rate:" + regValue[1] + "/PIP:" + regValue[2] + "/PEEP:" + regValue[3]
    }
    if (regString.match("HFOV") !== null) {
        return "FiO2:" + regValue[0] + "/Rate:" + regValue[1] + "/PIP:" + regValue[2] + "/PEEP:" + regValue[3]
    }
    if (regString.match("HFOV") !== null) {
        return "FiO2:" + regValue[0] + "/Amp:" + regValue[1] + "/MAP:" + regValue[2] + "/Freq:" + regValue[3]
    }
    if (regString.match("HFPPV") !== null) {
        return "FiO2:" + regValue[0] + "/Rate:" + regValue[1] + "/PIP:" + regValue[2] + "/PEEP:" + regValue[3]
    }
    return qspesettingarray.value
}
