import moment from 'moment'

export function LabDataFormat(DataList, startTime, endTime, fiterString) {
    if (Array.isArray(DataList)) {
        const filterDataList = DataList.filter(function (info, index, arr) {
            const time = moment((info.ORRESDT.toString() + info.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
            return !(info[fiterString] === null || info[fiterString] === undefined || info[fiterString].toString().trim() === "") && startTime <= time && time < endTime
        })
        const mapFilterDataList = filterDataList.map(function (info, index, arr) {
            console.log(info[fiterString])
            return{
                Data:info[fiterString].toString().trim(),
                Time:moment((info.ORRESDT.toString() + info.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
            }
        })
        return mapFilterDataList
    }
    return null
} 