import React, { Component } from 'react'
import * as d3 from "d3";
import './Watervaluechart.scss'
import { Modal } from 'antd'
// import { getRandomfornumber } from '../../Function/Wardstate'
import moment from 'moment';

class Watervaluechart extends Component {
    state = {
        visible: false,
        select: null,
        rowdata: null
    };
    showModal = () => {
        this.setState({ visible: true, });
    };

    handleOk = e => {
        this.setState({ visible: false, });
    };

    handleCancel = e => {
        this.setState({
            visible: true,
            select: null,
            rowdata: null
        });
    };
    componentDidUpdate() {
        const { svg_key } = this.props
        d3.select("#" + svg_key).remove()
        this.drawChart()

    }
    componentDidMount() {
        this.drawChart();
    }

    componentWillUnmount() {
    }

    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 130
            }
        }
        else {
            if (axisBot) {
                return 130
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 5
            }
            else {
                return 5
            }
        }
    }

    drawChart() {

        const { svg_key, id_key, WaterValueData, caseinfo } = this.props
        const width = (window.screen.availWidth - 50) / 2 - 50, height = 400, max = 280, min = 0
        const Feed = WaterValueData.Feed
        const IV = WaterValueData.IV
        const Other = WaterValueData.Other



        const shiftDays = Feed.length;
        // console.log(shiftDays)


        const rectwidth = (width / shiftDays) - 2

        var x = d3.scaleLinear()
            .domain([0, shiftDays])
            .range([0, width]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])



        var yAxis = d3.axisLeft(y)
            .ticks(14)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisBottom = d3.axisBottom(x)
            .ticks(shiftDays)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) {
                if (d % 10 === 0) {
                    if (d > 0) {
                        return 'D' + d
                    }
                }
                return null;
            });


        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width + 80, height + 50])
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 30 + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");

        svg.append("g")
            .call(xAxisBottom)
            .attr("class", "axis");


        for (let index = 0; index <= 14; index++) {
            //上下虛線
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(index * 20))
                .attr('x2', width)
                .attr('y2', y(index * 20))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }

        for (let index = 0; index < shiftDays; index++) {
            if (index % 10 === 0 && index > 0) {
                svg.append('line')
                    .attr('x1', x(index))
                    .attr('y1', y(min))
                    .attr('x2', x(index))
                    .attr('y2', y(max))
                    .style('stroke', 'rgba(187, 187, 187, 1)')
                    .style('stroke-width', 1)
                    .style('stroke-dasharray', 5.5);
            }
        }
        function sumCalories(feed, iv, other) {
            if (Number(feed) >= 0 && Number(iv) >= 0 && Number(other) >= 0) {
                return Number(feed) + Number(iv) + Number(other)
            }
            return 0
        }
        function formatNumber(value) {
            if (Number(value) >= 0) {
                return Number(value)
            }
            return 0
        }
        const select = this.state.select
        if (select === null || select === undefined || select === "") {
            d3.selectAll("rect#A123").remove()
        }
        console.log(Feed)
        for (let index = 0; index < shiftDays; index++) {
            const FeedRowData = formatNumber(Feed[index].WaterWeightValue)
            const IVRowData = formatNumber(IV[index].WaterWeightValue)
            const OtherRowData = formatNumber(Other[index].WaterWeightValue)
            const Calories = sumCalories(Feed[index].Calories, IV[index].Calories, Other[index].Calories)
            const time = Feed[index].startTime
            const orindex = index + 0.5
            const rowdata = {
                Feed: FeedRowData,
                IV: IVRowData,
                Other: OtherRowData,
                Calories: Calories,
                time: time
            }
            // console.log(FeedRowData)
            // console.log(IVRowData)
            // console.log(index)
            // console.log(Calories)
            var svgrect = svg.append('g').on('click', () => {
                if (Number(Calories) >= 0) {
                    this.setState({ visible: true, select: index, rowdata: rowdata });
                }
            })
            if (index === select) {
                svgrect.append('rect')
                    .attr('id', 'A123')
                    .attr('width', x(index + 1) - x(index))
                    .attr('height', y(0) - y(FeedRowData + IVRowData + OtherRowData))
                    .attr('x', x(index))
                    .attr('y', y(FeedRowData + IVRowData + OtherRowData))
                    .style('fill', 'rgba(0,0,0,0)')
                    .style('stroke', '#F5A623')
                    .style('stroke-width', 2)
                    .style('cursor', 'pointer');
            }
            else {
                svgrect.append('rect')
                    .attr('id', 'A123')
                    .attr('width', rectwidth + 2)
                    .attr('height', y(0) - y(FeedRowData + IVRowData + OtherRowData))
                    .attr('x', x(orindex) - (rectwidth / 2) - 1)
                    .attr('y', y(FeedRowData + IVRowData + OtherRowData))
                    .style('fill', 'rgba(0,0,0,0)')
                    .style('stroke', 'rgba(0,0,0,0)')
                    .style('stroke-width', 2)
                    .style('cursor', 'pointer')
                    .style('opacity', 1);
            }
            console.log(FeedRowData + IVRowData + OtherRowData)
            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(FeedRowData + IVRowData + OtherRowData))
                .attr('x2', x(orindex))
                .attr('y2', y(FeedRowData + IVRowData))
                .style('stroke', '#C3EFFF')
                .style('stroke-width', rectwidth)

            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(FeedRowData + IVRowData))
                .attr('x2', x(orindex))
                .attr('y2', y(FeedRowData))
                .style('stroke', '#3B97E1')
                .style('stroke-width', rectwidth)


            svgrect.append('line')
                .attr('x1', x(orindex))
                .attr('y1', y(FeedRowData))
                .attr('x2', x(orindex))
                .attr('y2', y(0))
                .style('stroke', '#E3CA6D')
                // .style('stroke', 'red')
                .style('stroke-width', rectwidth)
        }
    }

    ModalRender() {
        function formatNumber(value) {
            if (Number(value) >= 0) {
                return Number(value)
            }
            return 0
        }
        const RowData = this.state.rowdata
        console.log(RowData)
        if (RowData === null || RowData === undefined) { return null }
        const Feed = formatNumber(RowData.Feed)
        const IV = formatNumber(RowData.IV)
        const Other = formatNumber(RowData.Other)
        const Calories = formatNumber(RowData.Calories)
        const time = moment(RowData.time).format("YYYY/MM/DD")

        // const select = this.state.select
        // const waterdata = this.props.watervalueData
        // const car = waterdata[select].Caloriessum
        return (
            <Modal
                title={null}
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                width={240}
                footer={null}>
                <div style={{ marginTop: "20px" }}>{time}</div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 100px" }}>
                    <div style={{ backgroundColor: "rgba(113,176,228,0.43)" }}>項目</div>
                    <div style={{ backgroundColor: "rgba(113,176,228,0.43)" }}>量</div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 100px" }}>
                    <div style={{ border: "1px solid rgba(238,238,238,1)", borderRightWidth: "0px" }}>其他</div>
                    <div style={{ border: "1px solid rgba(238,238,238,1)" }}>{Other}{"  "}ml/kg/day</div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 100px" }}>
                    <div style={{ border: "1px solid rgba(238,238,238,1)", borderRightWidth: "0px" }}>輸液</div>
                    <div style={{ border: "1px solid rgba(238,238,238,1)" }}>{IV}{"  "}ml/kg/day</div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 100px" }}>
                    <div style={{ border: "1px solid rgba(238,238,238,1)", borderRightWidth: "0px" }}>進食</div>
                    <div style={{ border: "1px solid rgba(238,238,238,1)" }}>{Feed}{"  "}ml/kg/day</div>
                </div>
                <div>總熱量:{Calories}{"  "}Kcal/kg/day</div>
            </Modal>
        );
    }
    render() {
        const { id_key } = this.props
        const model = this.ModalRender()
        return (
            <div id={id_key}>
                {model}
            </div>
        );
    }
}

export default Watervaluechart
